.header-div{
    top:4em;
    padding-bottom:6em;
    position:sticky;
}
.header-links{
    position:relative;
    background-color:rgba(53, 48, 48, 0.291);
    color:rgb(198, 240, 221);
    padding:1em;
    border-radius:10px;
    text-decoration: none;
    margin-right:1em;
}
.header-links:hover{
    animation-name:bigger;
    background-color:rgb(53, 48, 48);
}
@keyframes bigger{
    from{
        transform:translateY(0px);
    }
    to{
        transform:translateY(40px);
    }
}